import { NOTIFY_USER } from "../actions/types";

const initialState = {
  message: "",
  messageType: null
};
// dispatch
export default (state = initialState, action) => {
  switch (action.type) {
    case NOTIFY_USER:
      return {
        ...state,
        message: action.message,
        messageType: action.messageType
      };
    default:
      return state;
  }
};
